body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.breed-selector {
  margin-bottom: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.cat-details {
  align-items: center;
  display: flex;
  overflow: hidden;
}

.cat-image {
  width: 600px;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
  flex-shrink: 0;
  margin-right: 20px;
}

.cat-description {
  word-wrap: break-word;
  max-width: calc(100% - 220px);
  flex: 0 auto;
}

